import { useEffect, useState } from "react";

import Section from "components/Section";
import { Icon } from "Icons";

import Auth from "../components/Navbar/Auth";

import api from '../services/api';

import spinner from '../img/spinner.svg'

function Home() {
	const [loading, setLoading] = useState(false);
	const [songName, setSongName] = useState('');
	const [songs, setSongs] = useState([]);
	const [notice, setNotice] = useState(false);

	const loadSongs = () => {
		api.get('songs').then(response => {
			setSongs(response.data);
			setLoading(false);
		});
	}

	useEffect(() => {
		setLoading(true)
		loadSongs()
	}, []);

	const handleSearchSong = () => {
		setLoading(true)
		setNotice(false)

		if (songName.length <= 0) {
			loadSongs()
		} else {
			api.get('songs', {
				params: {
					title_like: songName,
				}
			}).then(response => {
				if (response.data.length <= 0) {
					setNotice(true)
					setTimeout(function(){
						setLoading(false)
					}, 200)
				} else {
					setSongs(response.data);
					setTimeout(function(){
						setLoading(false)
					}, 200)
				}
			});
		}

	}

	return (
		<>
			<nav className="search ml-sm h-[3.75rem] flex items-center justify-between relative z-10">
				<div className="mr-auto relative">
					<label htmlFor="search-input" className="text-black w-12 h-10 flex items-center justify-center absolute top-0 left-0">
						{ loading ? <img src={spinner} alt="loading"/> : <Icon size={24} name="search" />}
					</label>
					<input
						value={songName}
						onChange={(event) => setSongName(event.target.value)}
						autoFocus={true}
						type="text"
						id="search-input"
						className="h-10 pl-12 outline-none text-black font-medium bg-white rounded-3xl text-sm placeholder-black/50 max-w-full w-[22.75rem]"
						placeholder="Buscar pelo nome da música" />

					<button onClick={handleSearchSong} className="ml-4">
						{ loading ? 'Buscando....' : 'Buscar'}
					</button>
				</div>
				<Auth />
			</nav>

			{notice &&
				<div className="grid gap-y-8 ml-sm mt-2">
					<span className="notice">Desculpe, não encontramos nenhuma música com termo informado.</span>
				</div>
			}

			{!loading && !notice &&
				<div className="grid gap-y-8">
					<Section
						items={songs}
					/>
				</div>
			}
		</>

	)
}

export default Home
