import { Icon } from "Icons";
import { useDispatch, useSelector } from "react-redux";
import { setCurrent } from "stores/player";

function SongItem({ item }) {

    const dispatch = useDispatch()
    const { current, playing, controls } = useSelector(state => state.player)
    const imageStyle = item => {
        switch (item.type) {
            case 'artist':
                return 'rounded-full'

            case 'podcast':
                return 'rounded-xl'

            default:
                return 'rounded'
        }
    }

    const updateCurrent = () => {
        if (current.id === item.id) {
            if (playing) {
                controls.pause()
            } else {
                controls.play()
            }
        }
        else {
            dispatch(setCurrent(item))
        }
    }

    const isCurrentItem = (current?.id === item.id && playing)

    return (
        <div
            key={item.id}
            className={"song p-4 rounded hover:bg-active group bg-footer"}
        >
            <div className="pt-[100%] relative mb-4">
                <img src={item.image} className={`cover absolute inset-0 object-cover w-full h-full ${imageStyle(item)}`} alt="Parodify" />
                <button
                    onClick={updateCurrent}
                    className={`${isCurrentItem ? 'pause' : 'play'} w-10 h-10 rounded-full bg-primary absolute bottom-2 right-2 items-center justify-center flex`}>
                    <Icon name={isCurrentItem ? 'pause' : 'play'} size={16} />
                </button>
            </div>
            <h6 className="title overflow-hidden overflow-ellipsis whitespace-nowrap text-base font-semibold">
                {item.title}
            </h6>
            <p className="artist line-clamp-2 text-link text-sm mt-1">
                {item.artist}
            </p>
        </div>
    )
}

export default SongItem
